import React, { Fragment } from "react";
import classNames from "classnames";
import style from "./style.scss";

export default ({ Icon, classCss = "", isRound = true }) => (
  <Fragment>
    {isRound ? (
      <div className={classNames(style.divIcon, "rounded-circle")}>
        <span className={classNames(style.icon, classCss)}>
          <Icon />
        </span>
      </div>
    ) : (
      <span className={classCss}>
        <Icon />
      </span>
    )}
  </Fragment>
);
